import React, { useState, useEffect } from "react";
import axios from "axios";

import { LoadScript, GoogleMap, Marker } from "@react-google-maps/api";

const CLIENT_ID = "AIzaSyBHBras2njADzwwxTD8cAFCTL8IROUGP_U";

const API_URL = "https://maps.googleapis.com/maps/api/geocode/json?";


type MapProps = {

 
    location:string;

  };
  const Map: React.FC<MapProps> = ({
    location,
    
  
  }) => {
  const [center, setCenter] = useState({ lat: null, lng: null });
  const [mapLoading,setMapLoading]=useState(false);

  useEffect(() => {
    setMapLoading(true);
    async function fetchData() {
      var result = await axios(
        `${API_URL}${`address=${location}`}${`&key=${CLIENT_ID}`}`
      );

      if (result.data.results.length > 0) {
        setCenter({
          lat: result.data.results[0].geometry.location.lat,
          lng: result.data.results[0].geometry.location.lng,
        });
      }
      setMapLoading(false);

    }
    fetchData();
  }, [location]);

  return (
    <div className="wrapper">
        {(mapLoading ) && (<div style={{width:"50px",height:"50px",margin:"auto"}}className="loader"></div>)}{/** short circuit */}

      {center.lat ? (
        <LoadScript googleMapsApiKey="AIzaSyBHBras2njADzwwxTD8cAFCTL8IROUGP_U">
          <GoogleMap
            id="map"
            center={center}
            zoom={10}
            mapContainerStyle={{
              height: "18vh",
              width: "66vh",
              borderRadius: "10px",
            }}
          >
            {/*  Marker component */}
            <Marker position={center} />
          </GoogleMap>
        </LoadScript>
      ) : (
        <div></div>
      )}


    </div>
  );
}

export default Map;
