import React from "react";
import LogoImg from "../Images/animal.png";
import LogoText from "../Images/logoText.png";

import LogoBg from "../Images/rect.png";
import { useHistory } from "react-router";
import {Wrapper,ButtonWrapper,SearchAreaWrapper} from './SearchArea.styles'

type SearchAreaProps = {

    callback: (e: React.MouseEvent<HTMLButtonElement>) =>void;

    setTextInput:any,
    setDropDownInput:any,
    setCurrentPage:any,
    fromHome:boolean;

  };

  const dropDownOptions = [
    { title: "Pattern", id: 8961198 },
    { title: "Camping", id: 1114848 },
    { title: "Wings", id: "KizanWcExgU" },
    { title: "Landscape", id: "11649432" },
    { title: "Vaccine", id: "KXEAgXRhxtE" },
    { title: "Gaming", id: "9415206" },
  ];
  

const SearchArea: React.FC<SearchAreaProps> = ({
    setTextInput,
    setDropDownInput,
    callback,
    setCurrentPage,
    fromHome
  
  }) => {

    const history = useHistory();

    const handleSearch=(e: React.MouseEvent<HTMLButtonElement>)=>{
        history.push("/p-home"); //problem?
        callback(e);


        setCurrentPage(1);

    }

return(
    <Wrapper>
              <SearchAreaWrapper fromHome={fromHome}>

<div style={{display:"flex"}} className="searchArea-wrapper">
      <div className="logo-wrapper">
        <div  className="logo">
        <img src={LogoImg} className="logoImg" alt="Logo" />
          <img src={LogoBg} className="logoBg" alt="LogoBack" />
        </div>
        <img
          src={LogoText}
          className="logoText"
          alt="LogoText"
        />
      </div>
      <div  className="inputs-wrapper">
       

       <input className="textInput" onChange={(e) => setTextInput(e.target.value)}type="text"  name="text input"/>
 
 
 <select className="dropdownInput"onChange={(e) => setDropDownInput(e.target.value)} name="dropdown input" >
   {dropDownOptions.map((op)=>{
       return  <option value={op.id}>{op.title}</option>
   })}
   
   </select>
       </div>


       <ButtonWrapper  correct={true}
        userClicked={false}
      >
       <button onClick={handleSearch}  className="searchBtn"type="button"><a>SEARCH</a></button>
        </ButtonWrapper>
</div>
   

      </SearchAreaWrapper>



</Wrapper>
)
    


}

export default SearchArea;