import React from "react";
import SearchArea from "../Components/SearchArea";


type SearchProps = {

    callback: (e: React.MouseEvent<HTMLButtonElement>) =>void;
    setTextInput:any;
    setDropDownInput:any;
    setCurrentPage:any;
    fromHome:boolean;

  };

const Search: React.FC<SearchProps> = ({
    callback,
    setTextInput,
    setDropDownInput,
    setCurrentPage,
    fromHome
    
  }) => (


    <div>
 <SearchArea fromHome={fromHome} callback={callback}  setCurrentPage={setCurrentPage}setTextInput={setTextInput} setDropDownInput={setDropDownInput}/>

    </div>
  )

;

export default Search;