import React from "react";

import {PhotoObj} from "../App";
import SearchArea from "../Components/SearchArea"; 
import MyModal from "../Components/MyModal"; 
import Pagination from "../Components/Pagination";
import {Wrapper} from './Home.styles'

  declare module "react" {
    interface ImgHTMLAttributes<T> extends HTMLAttributes<T> {
      username?: string;
      userfirstname?: string;
      userphotosource?: string;
      location?: string;
    }
    
  }

type HomeProps = {

    callback: (e: React.MouseEvent<HTMLButtonElement>) =>void;
    photos:PhotoObj[];
    setTextInput:any;
    setDropDownInput:any;
    loading:boolean; 
    error:boolean; 
    setCurrentPage:any;
    fromHome:boolean;
    textInput:string;
    dropDownInput:string;
    currentPage:number;
    pages:number;
    startSearch:(a:string,b:string,c:string)=>void;
    totalResults:number;

  };
 
const Home: React.FC<HomeProps> = ({
    photos,
    callback,
    setTextInput,
    setDropDownInput,
    loading,
    error,
    setCurrentPage,
    fromHome,
    textInput,
    dropDownInput,
    currentPage,
    pages,
    startSearch,
    totalResults
  }) => {

    const [open, setOpen] = React.useState(false);
const [modalInfo, setModalInfo] = React.useState({username: "",
location: "",
userfirstname: "",
userphoto: "",
userphotosource: "",
photosrc:""});



const handleOpen = (e:any) => {
    
    setModalInfo({
      username: e.getAttribute("username"),
      location: e.getAttribute("location"),
      userfirstname: e.getAttribute("userfirstname"),
      userphoto: e.getAttribute("userphotosource"),
      photosrc: e.getAttribute("src"),
      userphotosource: e.getAttribute("userphotosource"),
    });
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };



    return(<Wrapper>

        <div>
             <SearchArea fromHome={fromHome} setCurrentPage={setCurrentPage}callback={callback}   setTextInput={setTextInput} setDropDownInput={setDropDownInput}/>
            {/*  if no errors and loading comğleted show images their modal and pagination*/}
            
             {(!loading && !error) ? 
               (<div>

<div className="imagesDiv-wrapper">
              <div className="imagesDiv">
                  {photos ? (photos.map((photo, index) => (
                  <div >
<img
                    alt="grid photo"
                    className="photo"
                    onClick={(e) => handleOpen(e.target)}
                    key={index}
                    username={photo.username}
                    userfirstname={photo.userfirstname}
                    userphotosource={photo.userphotosource}
                    location={photo.location}
                    src={photo.photosrc}
                  />
                

                  </div>
              
  
                ))):"no photo"}

      <MyModal
      open={open}
       modalInfo={modalInfo}
        closeHandle={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
      </MyModal>

              </div>
            </div>
  

{totalResults > 3 ? (
        <Pagination currentPage={currentPage} textInput={textInput} dropDownInput={dropDownInput} 
        startSearch={startSearch}
        
        pages={pages}
        setCurrentPage={setCurrentPage}
        
        />
      ) : (
        ""
      )}
       
               </div>)
               : 
                (loading && !error && <div className="loader"></div>) 

            }
  
 
        </div>
        {photos.length===0 && <div className="error-wrapper">  <p style={{ fontFamily: "Open Sans" }}>Nothing Found</p></div>}

        </Wrapper>
        
        );
    

                  };

export default Home;
