import React from "react";
import {Wrapper} from './Pagination.style'

type PaginationProps = {


  textInput:string;
  dropDownInput:string;
  currentPage:number;
  pages:number;
  setCurrentPage:any;
  startSearch:(a:string,b:string,c:string)=>void;

};

const Pagination : React.FC<PaginationProps> = ({
 
  textInput,
  dropDownInput,
  currentPage,
  pages,
  setCurrentPage,
  startSearch

}) => {
  const pageLinks = []; //number links for pagination bar ,li tags for particular page number


  function nextPage(pageNumber:number) {
    startSearch(textInput, dropDownInput, pageNumber.toString());
    setCurrentPage(pageNumber);
  }


  function linkHandle(index:number) {
    nextPage(index);
  }

  for (let i = 1; i <= pages + 1; i++) {
    let active = currentPage === i ? "active" : "";
    let invisible = i > 5 ? "invisible" : "";
    pageLinks.push(
      <li
        key={i}
        className={`${active}${invisible}`}
        onClick={() => linkHandle(i)}
      >
        <a>{i}</a>
      </li>
    );
  }

  const prevHandle = () => {
    nextPage(currentPage - 1);
  };
  const nextHandle = () => {
    nextPage(currentPage + 1);
  };

 
  return (    <div style={{ backgroundColor: "#ffff" }}>
    <Wrapper>
  <div className="row">
    <ul className="pagination">
      {currentPage > 1 ? (
        <li onClick={prevHandle}>
          <a>Prev</a>
        </li>
      ) : (
        ""
      )}
      {pageLinks}
    
      {currentPage < pages + 1 ? (
        <li onClick={nextHandle}>
          <a>Next</a>
        </li>
      ) : (
        ""
      )}
    </ul>
  </div>
  </Wrapper>
</div>);
}


export default Pagination;