//import Font from "./Images/Open_Sans";

import styled from 'styled-components';

export const Wrapper =styled.div ` 
padding: 2%;

    background-color: #050417;

text-align: center;

p{
    font-size:1rem;

}

`

//styled component with props
type ButtonWrapperProps={
    correct:boolean,
    userClicked:boolean,
}


type SearchAreaStyleProps={
    fromHome:boolean,
}

export const ButtonWrapper =styled.div <ButtonWrapperProps>` 

transition:all 0.3s ease;
:hover{
    opacity:0.4;
}
a{
     display:inline-block;
 padding:0.3em 1.2em;
 margin:0 0.3em 0.3em 0;
 border-radius:2em;
 box-sizing: border-box;
 text-decoration:none;
 font-weight:300;
 color:#FFFFFF;
background-color: #2A2B8D;
 text-align:center;
 transition: all 0.2s;
}

a:hover{
 background-color:#4095c6;
}



button{
    cursor:pointer;
  
    button{
    cursor:pointer;
  
    background:red;
   
height:200px;
width:200px;


}



};

`


export const SearchAreaWrapper =styled.div <SearchAreaStyleProps>` 

.searchArea-wrapper{
    display: "flex";
flex-direction: ${({ fromHome }) =>
      fromHome
        ?'row'//home
        : 'column'
};
width: ${({ fromHome }) =>
      fromHome
        ?"100%"//home
        : "400px"
};
margin:auto;
margin-top: ${({ fromHome }) =>
      fromHome
        ?"inherit"//home
        : "10%"
};
background-color:"#050417";
align-items:center;

height: ${({ fromHome }) =>
      fromHome
        ?"1.75em"//home
        : "inherit"
};

justify-content: ${({ fromHome }) =>
      fromHome
        ? "space-around"//home
        : "inherit"
};

    display: "flex";


}
.dropdownInput { grid-area: dd;  
    width: 57vh;
    height:50px;
    background-color: "#FFFFFF";
    border-radius: "4px";
    font-size: 14;
    font-weight: 600;
    font-family:"Open Sans";
font-style: "normal";
padding-left:1rem;

 }
.textInput { grid-area: txt;  height:45px !important;
     background-color: "#FFFFFF";
     width: 57vh;

     height:45px;
     border-radius: "4px";
    font-size: 14;
    font-weight: 600;
    font-family:"Open Sans";
font-style: "normal";
padding-left:1rem;

  
 }
.inputs-wrapper{
    grid-template-areas:
    'txt dd';
    grid-gap: 10px;  
}


.logo-wrapper{
    display: ${({ fromHome }) =>
      fromHome
        ?'flex'//home
        : 'initial'
};

justify-content: ${({ fromHome }) =>
      fromHome
        ? "center"//home
        : "initial"
};

align-items: ${({ fromHome }) =>
      fromHome
        ? "center"//home
        : "initial"
};

margin-bottom: ${({ fromHome }) =>
      fromHome
        ? "0px"//home
        : "15%"
};
text-align: ${({ fromHome }) =>
      fromHome
        ? "initial"//home
        : "center"
};


}
.logoImg {
  position: absolute;
  margin-top: 0.9%;
  margin-left: 0.7%;
  z-index: 2;
}
.logoBg {
  position: relative;
  display: inline;
  z-index: 1;
}

.logoText{
    display: ${({ fromHome }) =>
      fromHome
        ? "none"//home
        : "initial"
};

}




.inputs-wrapper{
    display: ${({ fromHome }) =>
      fromHome
        ?'grid'//home
        : 'flex'
};
flex-direction: ${({ fromHome }) =>
      fromHome
        ?''//home
        : 'column'
};

}



.textInput{
    background-color: "#FFFFFF";
    border-radius: 4px;
    font-size: 14;
    font-weight: 600;
    height: 50px;
    width: 400px;
   
margin-bottom: ${({ fromHome }) =>
      fromHome
        ?'0px'//home
        : '3%'
};

margin: auto;

}

.dropdownInput{
    background-color: "#FFFFFF";
    border-radius: 4px;
    font-size: 14;
    font-weight: 600;
    height: 50px;
    width: 418px;
   
    margin-left: ${({ fromHome }) =>
      fromHome
        ?'1.875rem'//home
        : 'initial'
};
margin-bottom: ${({ fromHome }) =>
      fromHome
        ?'6%'//home
        : '23%'
};



margin: auto;

}
.searchBtn{

    background-color: #7fffd400;
    border: none;
    margin-top: ${({ fromHome }) =>
      fromHome
        ?''//home
        : '22%'
};
   
}
.searchBtn a{
    height:50px;
    width:256px;
    margin:auto;
    padding: 1rem;
    font-family: Open Sans;
font-style: normal;
font-weight: 800;
font-size: 14px;
line-height: 19px;
letter-spacing: 0.1em;

color: #FFFFFF;
}



`


type InputAreaStyleProps={
    fromHome:boolean,
}

export const InputAreaWrapper =styled.div <InputAreaStyleProps>` 




`

