import React from "react";
import {Wrapper} from '../Pages/Home.styles'
import LocationIcon from "../Images/Union.png";
import Modal from "@material-ui/core/Modal";
import Map from "../Components/Map";


type ModalProps = {

    modalInfo:any;//model ınfo object
    closeHandle: () => void;
    open:boolean;//state
   

  };

  const modalStyles =({
    bg: {
      position: 'absolute' as 'absolute',
      top: "6%",
      left: "33%",
      width: "65vh",
      height: " 80vh",
      backgroundColor: " #FFFFFF",
      padding: "20px 30px",
      borderRadius: "10px",
    },
    modalPhoto: {
        width: "65vh",
        height: "80vh"
      },
    button: {
      width: "16vh",
      height: "5vh",
      fontSize: ".7em",
      backgroundColor: "#FFFFFF",
      border: "2px solid #2A2B8D",
      borderRadius: "10px",
      fontFamily: "Open Sans",
      color: "#2A2B8D",
    },
  });
  const MyModal: React.FC<ModalProps> = ({
    modalInfo,
    closeHandle,open
 
  }) => {
      
 
    var modalBody = (
        <Wrapper>
        <div style={modalStyles.bg}className="modal-container" >
          <img
            alt="modal photo"
            style={{
              width: "66vh",
              height: " 45vh",
            }}
            className="modal-photo"
            src={modalInfo.photosrc}
          />
          <div className="userinfo-row">
            <img
              alt="username photo"
              className="user-photo"
              src={modalInfo.userphotosource}
            />
            <div className="user-names">
              <div className="firstName">{modalInfo.username}</div>
    
              <div className="userName">@{modalInfo.userfirstname}</div>
            </div>
    
            <div className="download-btn">
              <a
                href={modalInfo.photosrc}
                target="_blank"
                rel="noopener noreferrer"
                download
              >
               
              </a>
            </div>
          </div>
          {modalInfo.location ? (
            <div className="map-wrapper">
              <Map location={modalInfo.location}></Map>
              <div className="loc-wrapper">
              <img alt="icon" src={LocationIcon} />
                {modalInfo.location}
              </div>
            </div>
          ) : (
            <div className="modal-error">
              <p>Location information not available. </p>
            </div>
          )}
        </div>
        </Wrapper>
      );
    
    

  return (<div> 

<Modal
        open={open}
        onClose={closeHandle}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {modalBody}
      </Modal>
  </div>);
}


export default MyModal;