import { Switch, Route } from "react-router-dom";
import { HashRouter as Router } from "react-router-dom";
import {GlobalStyle,Wrapper} from "./App.styles";
import Search from "./Pages/Search";
import Home from "./Pages/Home";

import React, { useState } from "react";
import axios from "axios";

const API_URL = "https://api.unsplash.com/search/photos/?client_id=";
const CLIENT_ID = "5IzaDchWrL4MociV4hY0pXCviOe2hQP2TWjsh-629JQ";

  export type PhotoObj={

    username:string;
    location:string;
    userfirstname:string;
    userphoto:string;
    photosrc:string;
    userphotosource:string;
}


const App = () => {
  const [photos, setPhotos] = useState<PhotoObj[]>([]);

  const [totalResults, setTotalResults] = useState(0); //set in app ?
  const [textInput, setTextInput] = useState("");
  const [dropDownInput, setDropDownInput] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [error,setError]=useState(false);
  const [loading,setLoading]=useState(false);

const searchHandler=(e: React.MouseEvent<HTMLButtonElement>)=>{
  startSearch(textInput,dropDownInput,"1",0)
}


  const startSearch=async (
    textInput:string = " ",
    dropDownInput:string = " ",
    page:string = " ",
    firstSearch:number = 0
    
  )=>{
    setLoading(true);
    try {

      const { data } = await axios.get(
        `${API_URL}${CLIENT_ID}${`&query=${textInput}`}${`&collections=${dropDownInput}`}${`&page=${page}`}`
      );

      if (firstSearch) {
        setCurrentPage(1);
      }
      setLoading(false);
      setTotalResults(data.total);
    
      

    var photoList =  data.results.map((photo:any)=>({
        username:photo.user.username,
        userfirstname:photo.user.first_name,
        photosrc:photo.urls.small,
      location:photo.user.location,
      userphotosource:photo.user.profile_image.medium
      
    }))

      setPhotos(photoList);

    } catch (error) {
      console.error(error);
      setError(true);
      
    }
  }

  return (
      <div className="App">
 <GlobalStyle/>
    
    <Wrapper>
<Router>
      <div className="App">
        <Switch>
          <Route path="/p-search" exact>
          <Search fromHome={false}setCurrentPage={setCurrentPage} callback={searchHandler}  setTextInput={setTextInput} setDropDownInput={setDropDownInput} />

          </Route>

          <Route path="/p-home" exact>

            <Home 
            callback={searchHandler}
photos={photos} 
setTextInput={setTextInput} 
setDropDownInput={setDropDownInput}
loading={loading}
error={error}
setCurrentPage={setCurrentPage}
fromHome={true} 
textInput={textInput}   
dropDownInput={dropDownInput} 
currentPage={currentPage} 
pages={ Math.floor(totalResults / 10)}
startSearch={startSearch}
totalResults={totalResults}

          
               />
      

          </Route>
        </Switch>
      </div>
    </Router>

    </Wrapper>

       
      </div>
  );
}

export default App;

